import React from "react";

import { Document, Page } from 'react-pdf/dist/esm/entry.webpack'

class PdfViewer extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            file: "./Cosi_rossa_e_la_rosa.pdf",
            numPages: 1
        }

        this.onDocumentLoadSuccess = this.onDocumentLoadSuccess.bind(this)
    }

    onDocumentLoadSuccess = (rawData) => {
        let numPages = rawData._pdfInfo.numPages
        this.setState({ numPages });
    }

    render() {
        return <Document file={this.state.file} onLoadSuccess={this.onDocumentLoadSuccess}>
            <Page key={`page_1`} pageNumber={ 1 } />
        </Document>
    }
}

export default PdfViewer